.container {
	margin: 0 auto;
	padding: 0 15px;
	height: 100%;
	max-width: 1170px;
}

.orange {
	font-weight: inherit;
	font-size: inherit;
	color: $orange;
}

.l-blue {
	font-weight: inherit;
	font-size: inherit;
	color: $l-blue;
}

.scroll {
	overflow: hidden;
}

