@media (max-width: 769px) {
	.about-block {
		border-right: none;
		padding: 0 80px 25px 35px;
	}
}

@media (max-width: 630px) {
	.about-block__text {
		font-size: 15px;
	}
}

@media (max-width: 551px) {
	.about-block {
		padding-right: 35px;
		background: url("../images/images/back-condition-mob.svg") no-repeat center / 100% 100%;
	}

	.about-block__title {
		margin-top: 60px;
	}

	.about-block__text {
		margin-top: 30px;
	}
}

@media (max-width: 426px) {
	.about-block {
		padding: 0 15px 25px;
	}
}