@media (max-width: 1300px) {
	/* .ribbon {
		left: -100%;
		width: 1500px;
	} */

	.offer-logo {
		width: 500px;
		height: 500px;
	}
}

@media (max-width: 1190px) {
	.offer {
		padding: 130px 0 0;
		font-size: 38px;
	}

	.offer-img-block {
		height: 450px;
		max-width: 450px;
	}

	.offer-img {
		height: 400px;
		max-width: 400px;
	}

	.offer-logo {
		width: 450px;
		height: 450px;
	}
}

@media (max-width: 1000px) {
	.header {
		height: auto;
	}

	.offer-block {
		flex-direction: column-reverse;
		align-items: center;
		padding: 15px 0 0;
	}

	.offer-img-block {
		height: 420px;
		max-width: 420px;
	}

	.offer {
		padding: 0;
		text-align: center;
	}

	.offer-img {
		height: 380px;
		max-width: 380px;
	}

	.offer-logo {
		width: 420px;
		height: 420px;
	}
}

@media (max-width: 1024px) {
	.phone-link {
		right: 0;
		font-size: 18px;
	}
}

@media (max-width: 920px) {
	.logo {
		margin: 0 20px 0 0;
	}

	/* .ribbon {
		width: 1000px;
	} */

	.list-link {
		font-size: 14px;
	}
}

@media (max-width: 845px) {
	.list-link {
		font-size: 13px;
	}

	.list-item {
		&:not(:last-child) {
			margin-right: 18px;
		}
	}

	.phone-link {
		right: 0;
		font-size: 16px;
	}
}

@media (max-width: 769px) {
	.hamburger {
		display: inline-block;
	}

	.header {
		height: auto;
	}

	.container-header-grey-line {
		border-top: none;
		border-right: none;
	}

	.top-menu {
		top: 0;
		max-width: 85%;
	}

	.phone {
		right: 50%;
		transform: translateX(50%) translateY(-50%);
	}

	.phone-link {
		font-size: 22px;

		&:hover {
			&::after {
				width: 0;
			}
		}
	}

	.nav {
		position: absolute;
		left: -100%;
		top: 81px;
		z-index: 10;
		overflow: auto;
		width: 100%;
		height: 100%;
		background-color: rgba($orange, .93);
		backdrop-filter: blur(2px);
	}

	.menu-active {
		left: 0;
		transition-duration: .4s;
	}

	.list {
		flex-direction: column;
		align-items: center;
		padding: 40px 0 0;
		height: 380px;

		&-link {
			font-size: 20px;
			color: #fff;

			&:hover {
				&::after {
					width: 0;
				}
			}
		}
	}

	.list-item {
		&:not(:last-child) {
			margin: 0 0 30px;
		}
	}
}

@media (max-width: 580px) {
	.header {
		padding-bottom: 25px;
	}

	/* .ribbon {
		left: -50%;
		width: 500px;
	} */

	.hamburger {
		right: -10px;
	}

	.offer-img-block {
		height: 380px;
		max-width: 380px;
	}

	.offer-img {
		height: 320px;
		max-width: 320px;
	}

	.offer-logo {
		width: 380px;
		height: 380px;
	}
}

@media (max-width: 480px) {
	.top-menu {
		max-width: 80%;
	}

	/* .ribbon {
		left: -20%;
		width: 100px;
	} */

	.phone {
		display: none;
	}

	.phone-icon {
		display: inline-block;
	}

	.hamburger {
		right: -10px;
	}

	.offer {
		font-size: 30px;
	}

	.offer-img-block {
		height: 320px;
		max-width: 320px;
	}

	.offer-img {
		height: 300px;
		max-width: 300px;
	}

	.offer-logo {
		width: 260px;
		height: 260px;
	}
}

@media (max-width: 426px) {
	.offer {
		font-size: 26px;
	}

	.offer-img {
		height: 280px;
		max-width: 280px;
	}
}

@media (max-width: 376px) {
	.logo {
		width: 85px;
	}
}

@media (max-width: 321px) {
	.top-menu {
		max-width: 76%;
	}

	.phone-link {
		font-size: 18px;
	}

	.offer-img-block {
		height: 230px;
		max-width: 230px;
	}

	.offer-img {
		height: 230px;
		max-width: 230px;
	}

	.offer-logo {
		width: 220px;
		height: 220px;
	}
}