.section {
	position: relative;
	margin-top: 45px;
}

.section-fill-left {
	position: absolute;
	left: 0;
	z-index: -1;
	display: block;
	width: 50%;
	height: 100%;
	background-color: $grey;
}

.section-fill-right {
	position: absolute;
	right: 0;
	z-index: -1;
	display: block;
	width: 50%;
	height: 100%;
	background-color: $grey;
}

.section__container {
	padding: 130px 15px 77px;
}

.section__container--grey {
	background-color: $grey;
}

.section__content-block {
	display: flex;
}

.section__content-block--reverse {
	flex-direction: row-reverse;
}

.content-block__info-block,
.content-block__img-block {
	flex: 0 1 50%;
}

.content-block__img-block {
	position: relative;
	height: auto;
}

.section__title {
	margin-top: 15px;
	font-size: 40px;
	line-height: 1.25;
	text-transform: uppercase;
	color: $blue;
}

.section__list {
	margin-top: 45px;
}

.section__list-item {
	position: relative;
	padding: 0 0 0 23px;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.25;

	&::before {
		content: "";
		position: absolute;
		left: 3px;
		top: 4px;
		display: block;
		border-radius: 50%;
		width: 10px;
		height: 10px;
		background-color: $orange;
	}

	&:not(:last-child) {
		margin-bottom: 27px;
	}
}

.section__list-item--l-blue-dot::before {
	background-color: $l-blue;
}

.section__romb {
	position: absolute;
	left: 50%;
	top: 50%;
	z-index: 1;
	display: block;
	transform: translateX(-50%) translateY(-50%);
}

.section__romb-2--right {
	margin-left: 30px;
}

.section__romb-3--right {
	margin-left: 65px;
}

.section__romb-2--left {
	margin-left: -30px;
}

.section__romb-3--left {
	margin-left: -65px;
}

.section__romb-1 {
	width: 100%;
	height: 489px;
	max-width: 396px;
}

.section__romb-2 {
	width: 100%;
	height: 448px;
	max-width: 362px;
}

.section__romb-3 {
	width: 100%;
	height: 391px;
	max-width: 316px;
}

.section__main-img {
	position: absolute;
	top: 50%;
	z-index: 2;
	width: 100%;
	height: auto;
	max-width: 357px;
	object-fit: cover;
	transform: translateY(-50%);
}

.section__main-img--right {
	left: 30px;
}

.section__main-img--left {
	right: 50px;
}