.humburger {
	display: none;
}

.header {
	position: relative;
	padding: 30px 0 0;
	height: 651px;
}

.container-header-grey-line {
	border-top: 3px solid $grey;
	border-right: 3px solid $grey;
}

.ribbon {
	position: absolute;
	left: 0;
	top: 30px;
	display: block;
	width: 50%;
	height: 52px;
	background-color: $orange;
}

.menu {
	position: relative;
}

.top-menu {
	position: relative;
	left: 0;
	right: 50%;
	top: -3px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 52px;
	max-width: 81%;
	background-color: $orange;
}

.skew {
	position: absolute;
	right: -13px;
	top: 0;
	z-index: 1;
	width: 27px;
	height: 52px;
	background-color: $orange;
	transform: skew(25deg);
}

.nav {
	padding: 2px 0 0;
}

.list {
	display: flex;
	padding-right: 20px;
}

.list-item {
	list-style: none;

	&:not(:last-child) {
		margin-right: 30px;
	}
}

.list-link {
	position: relative;
	font-size: 15px;
	text-decoration: none;
	text-transform: uppercase;
	color: $blue;
	transition-duration: .13s;

	&::after {
		content: "";
		position: absolute;
		left: 0;
		bottom: 0;
		display: block;
		width: 0;
		height: 2px;
		background-color: #fff;
		transition-duration: .15s;
	}

	&:hover {
		color: #fff;
		transition-duration: .13s;

		&::after {
			width: 100%;
		}
	}
}

.phone {
	position: absolute;
	right: 5px;
	top: 50%;
	display: inline-block;
	transform: translateY(-50%);
}

.phone-icon {
	display: none;
}

.phone-link {
	position: relative;
	font-size: 22px;
	text-decoration: none;
	color: transparent;
	background: linear-gradient($blue, $l-blue);
	background-clip: text;
	transition-duration: .15s;

	&::after {
		content: "";
		position: absolute;
		left: 0;
		bottom: 2px;
		display: block;
		width: 0;
		height: 1px;
		background-color: $l-blue;
		transition-duration: .15s;
	}

	&:hover {
		color: transparent;
		background: linear-gradient($l-blue, $l-blue);
		background-clip: text;
		transition-duration: .15s;

		&::after {
			width: 100%;
		}
	}
}

.offer-block {
	position: relative;
	display: flex;
	justify-content: space-between;
	padding: 100px 0 0;
	height: auto;
}

.offer {
	padding: 100px 0 0;
	width: 100%;
	max-width: 570px;
	font-weight: 500;
	font-size: 40px;
	line-height: 1.45;
	text-transform: uppercase;
	color: $blue;
}

.offer-img-block {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 500px;
	max-width: 570px;
}

.offer-img {
	width: 100%;
	height: 500px;
	max-width: 500px;
	background: url("../images/images/flat.webp") no-repeat center / contain;
}

.offer-logo {
	position: absolute;
	z-index: -1;
	width: 550px;
	height: 550px;
	background: url("../images/logo-big.svg") no-repeat center / contain;
	object-fit: cover;
	opacity: .5;
	animation: offer-logo 135s linear 0s normal none infinite running;
}

@keyframes offer-logo {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(-360deg);
	}
}