.about-block {
	position: relative;
	border-right: 3px solid #f5f5f5;
	padding: 0 70px 45px 65px;
	width: 100%;
	background: url("../images/images/back-condition.svg") no-repeat center / 100% 100%;
}

.about-block__title {
	display: inline-block;
	margin: 75px 0 0;
	font-weight: 500;
	font-size: 26px;
	text-transform: uppercase;
	color: #fff;
}

.about-block__text {
	margin: 40px 0 0;
	max-width: 750px;
	font-size: 16px;
	line-height: 1.4;
	letter-spacing: .5px;
	color: #fff;
}