@media (max-width: 1025px) {
	.section__romb-1 {
		height: 430px;
		max-width: 337px;
	}

	.section__romb-2 {
		height: 389px;
		max-width: 303px;
	}

	.section__romb-3 {
		height: 332px;
		max-width: 257px;
	}

	.section-condition {
		margin-top: 50px;
	}

	.section__container {
		padding: 80px 15px 50px;
	}

	.section__list-item:not(:last-child) {
		margin-bottom: 20px;
	}

	.section__title {
		font-size: 36px;
	}

	.section__main-img {
		max-width: 310px;
	}
}

@media (max-width: 769px) {
	.section {
		overflow: hidden;
	}

	.section__main-img {
		left: -25px;
	}
}

@media (max-width: 746px) {
	.section__content-block {
		flex-direction: column;
		overflow: hidden;
	}

	.content-block__info-block,
	.content-block__img-block {
		flex: 0 1 100%;
	}

	.section__main-img {
		position: static;
		transform: none;
	}

	.content-block__img-block {
		position: absolute;
		top: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;
		opacity: .4;
		transform: translateY(-50%);
	}

	.content-block__img-block--right {
		right: -100px;
	}
	.content-block__img-block--left {
		left: -100px;
	}

	.section__container {
		padding: 25px 15px 50px;
	}
}

@media (max-width: 481px) {
	.about-block__text {
		font-size: 16px;
	}

	.section__title {
		font-size: 30px;
	}

	.section__list-item {
		z-index: 5;
		font-weight: 400;
	}
}